/* ## Tables
--------------------------------------------- */
table {
  background-color: #ffffff;
  border-collapse: collapse;
  border-spacing: 0;
  line-height: 1.618;
  margin-bottom: 40px;
  width: 100%;
  word-break: break-word;
}

tbody {
  border-bottom: 1px solid rgba(0, 10, 20, 0.075);
}

td,
th {
  text-align: left;
}

td {
  border-top: 1px solid rgba(0, 10, 20, 0.075);
  padding: 6px;
}

th {
  padding: 0 6px;
}

td:first-child,
th:first-child {
  padding-left: 0;
}

/* ## Accessibility
--------------------------------------------- */
.site-description,
.screen-reader-text,
.screen-reader-text span,
.screen-reader-shortcut,
.wp-custom-logo .site-title,
.wp-custom-logo .site-description {
  position: absolute !important;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  width: 1px;
  border: 0;
  overflow: hidden;
}

.screen-reader-text:focus,
.screen-reader-shortcut:focus {
  clip: auto !important;
  height: auto;
  width: auto;
  display: block;
  font-size: 16px;
  padding: 15px 23px 14px;
  color: #55575d;
  z-index: 100000;
  text-decoration: none;
  box-shadow: 0 0 2px 2px rgba(0, 10, 20, 0.1);
}

.more-link {
  position: relative;
  font-style: normal;
  font-size: 12px;
}

.genesis-skip-link {
  margin: 0;
}

.genesis-skip-link li {
  height: 0;
  width: 0;
  list-style: none;
}

