/* ## Pricing Table
--------------------------------------------- */
.pricing-table {
  display: block;
  align-items: center;
  padding: 2em 0 0;
  clear: both;
}

.pricing-table:before, .pricing-table:after {
  display: block;
  content: '';
}

.pricing-table:after {
  clear: both;
}

@media (min-width: 768px) {
  .pricing-table {
    display: flex;
    padding: 2em 0;
  }
}

.pricing-table > div {
  background-color: #f5f5f5;
  text-align: center;
  border-radius: 1px;
  border: 1px solid rgba(0, 10, 20, 0.075);
  margin: 0 auto 10%;
}

