  font-size: 14.4px;
  line-height: 1;
}

@media (min-width: 768px) {
  .genesis-nav-menu {
    text-align: right;
  }
}

.genesis-nav-menu .menu-item {
  display: block;
  margin-bottom: 0;
  padding: 10px 0;
  text-align: left;
  width: 100%;
  float: left;
  position: relative;
}

@media (min-width: 768px) {
  .genesis-nav-menu .menu-item {
    text-align: right;
    float: none;
    display: inline-block;
    padding: 0;
    width: auto;
  }
}

.genesis-nav-menu .menu-item:last-of-type a {
  padding-right: 0;
}

