/* ## Menu toggle
--------------------------------------------- */
.menu-toggle {
  height: 25px;
  height: 2.5rem;
  width: 25px;
  width: 2.5rem;
  padding: 20px 0;
  padding: 2rem 0;
  margin: auto;
  margin-left: 40px;
  margin-left: 4rem;
  cursor: pointer;
  background-color: transparent;
  display: block;
  position: relative;
  z-index: 99;
}

.menu-toggle:hover {
  outline: none;
  box-shadow: none;
}

@media (min-width: 768px) {
  .menu-toggle {
    padding: 30px 30px 30px 0;
  }
}

@media (min-width: 768px) {
  .menu-toggle {
    display: none;
  }
}

.menu-toggle span {
  display: block;
  position: absolute;
  height: 2px;
  background: #43454b;
  transition: background 0s 0.25s;
  width: 25px;
  width: 2.5rem;
  margin-top: -1px;
  border-radius: 1px;
}

.menu-toggle span::before, .menu-toggle span::after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #43454b;
  border-radius: 1px;
  content: " ";
}

.menu-toggle span::before {
  top: -7px;
  transition: top 0.25s 0.25s, transform 0.25s 0s;
}

.menu-toggle span::after {
  bottom: -7px;
  transition: bottom 0.25s 0.25s, transform 0.25s 0s;
}

.menu-toggle.activated span {
  background: none;
}

.menu-toggle.activated span::before {
  top: 0;
  transform: rotate(45deg);
  transition: top 0.25s 0s, transform 0.25s 0.25s;
}

